/**
 * Getter:      
 *
 * @returns {array}     
 */
const currencies = state => state.currencies;


/**
 * Getter:    
 *
 * @returns {string} -    
 */
const activeCurrency = state => state.activeCurrency;


/**
 * Getter:       
 *
 * @returns {object} -      
 */
const activeCurrencyData = state => state.currencies[state.activeCurrency];

export default {
  activeCurrency,
  activeCurrencyData,
  currencies,
};
