import log from '@/util/logger';

import { LOCAL_STORAGE_PREFIX } from '@/util/constants';


/**
 *  ,        
 *
 * @param {string} key - 
 */
const keyWithPrefix = key => `${LOCAL_STORAGE_PREFIX}${key}`;


/**
 *      
 *
 * @param {string} key - ,     
 * @param {*} value - ,   
 *
 * @since Version 1.0.0
 */
const setValue = (key, value) => {
  localStorage.setItem(keyWithPrefix(key), JSON.stringify(value));
};


/**
 *      
 *
 * @param {string} key - ,     
 * @param {*} data - ,   
 *
 * @since Version 1.0.0
 */
const setArray = (key, value) => {
  const array = value === null ? [] : value;
  localStorage.setItem(keyWithPrefix(key), JSON.stringify(array));
};


/**
 *      
 *
 * @param {string} key - ,     
 *
 * @since Version 1.0.0
 */
const getValue = (key) => {
  const fullKey = keyWithPrefix(key);
  const stored = localStorage.getItem(fullKey);
  if (stored !== null) {
    try {
      return JSON.parse(stored);
    } catch (error) {
      log.error(`Error in parsing value from local storage key ${fullKey}: ${error}`);
    }
  }
  return null;
};


/**
 *      
 *
 * @param {string} key - ,     
 *
 * @since Version 1.0.0
 */
const getArray = (key) => {
  const fullKey = keyWithPrefix(key);
  const stored = localStorage.getItem(fullKey);
  if (stored !== null) {
    try {
      return JSON.parse(stored);
    } catch (error) {
      log.error(`Error in parsing value from local storage key ${fullKey}: ${error}`);
    }
  }
  return [];
};


/**
 *     
 *
 * @param {string} key - ,     
 *
 * @since Version 1.0.0
 */
const remove = (key) => {
  localStorage.removeItem(keyWithPrefix(key));
};

export default {
  getArray,
  getValue,
  remove,
  setArray,
  setValue,
};
